.app {
  position: relative;
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.app__header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 6vmin);
  color: white;
  font-family: 'Cinzel', serif;
  text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
  0px 8px 13px rgba(0,0,0,0.1),
  0px 18px 23px rgba(0,0,0,0.1);
}

.app__background {
  background: linear-gradient(rgba(69, 69, 69, 0.95), rgba(69, 69, 69, 0.95)), url("assets/images/background.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.app__overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.5;
  z-index: 2;
}

.app-link {
  color: #61dafb;
}
